  static patchProposal(clientID: string, proposalID: string, data: any) {
    if (data && data['sales_channel'] && data['sales_channel']['type']) {
      if (data['sales_channel']['type'] === 'agent') {
        data['sales_channel']['display_name'] = data['sales_channel']['agent_name']
      } else if (data['sales_channel']['type'] === 'bdo') {
        data['sales_channel']['display_name'] = data['sales_channel']['bdo_name']
      } else if (data['sales_channel']['type'] === 'broker') {
        data['sales_channel']['display_name'] = data['sales_channel']['contact_person']
      }
    }
    const PROPOSAL_URL =
      API_URL + 'benefit-plans/proposals/' + clientID + '/' + proposalID;
    return fetch(PROPOSAL_URL, {
      method: 'PATCH',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
        return error;
      });
  }

