  static getClients(
    limit: number,
    skip: number,
    filter: object = {},
    order: string[] = [],
  ) {
    let filters = {
      limit: limit,
      skip: skip,
    };

    if (Object.entries(filter).length !== 0) {
      let where = {};
      for (let x in filter) {
        const fil = filter[x];
        if (fil['columnName'] && fil['value']) {

          if (['registered_name', 'corporate_account_no'].includes(fil['columnName'])) {
            where[fil['columnName']] = {
              like: fil['value'],
              options: "i"
            };
          } else if (['prev_hmo_effectivity_date', 'prev_hmo_expiry_date'].includes(fil['columnName'])) {
            let date = fil['value'];

            if (moment.isMoment(fil['value'])) {
              date = fil['value'].format('YYYY-MM-DD');
            }

            where[fil['columnName']] = {
              like: date,
              options: "i"
            };
          } else {
            where[fil['columnName']] = fil['value'];
          }
        }
      }
      filters['where'] = where;

    }

    if (order.length === 1) {
      filters['order'] = order[0];
    } else {
      filters['order'] = order;
    }

    let CLIENTS_URL =
      API_URL + `clients/?filter=${JSON.stringify(filters)}&count=${true}`;

    console.log(CLIENTS_URL);
    return fetch(CLIENTS_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
        return error;
      });
  }

