  static listClients(
    query: string | null,
    limit: number,
    skip: number,
    filter: any,
    order: string[] = [],
  ) {

    let query_str = query ? `&query=${query}` : '';
    let filter_str = ''
    if (filter && filter !== undefined && filter.length > 0) {
      filter.forEach(item => {
        if (item['columnName'] === 'prev_hmo_effectivity_date') {
          filter_str = filter_str + `&eff_date=${item['value'].format('YYYY-MM-DD')}`
        } else if (item['columnName'] === 'prev_hmo_expiry_date') {
          filter_str = filter_str + `&exp_date=${item['value'].format('YYYY-MM-DD')}`
        } else if (item['columnName'] === 'status') {
          if (item['value'] === 'All') {
            filter_str = filter_str
          }
          else {
            filter_str = filter_str + `&${item['columnName']}=${item['value']}`
          }
        } else {
          filter_str = filter_str + `&${item['columnName']}=${item['value']}`
        }

      })
    }

    let sort_str = ''
    if (order && order.length === 1) {
      let order_split = order[0].split(' ')
      sort_str = '&sort=' + order_split[0] + ' ' + order_split[1]
    }

    let CLIENTS_URL =
      API_URL +
      'clients/list';
    if (limit !== 0) {
      CLIENTS_URL += '?filter[limit]=' + limit + '&filter[skip]=' + skip + query_str + filter_str + sort_str;
    } else {
      CLIENTS_URL += '?filter[skip]=' + skip + query_str + filter_str + sort_str;
    }

    console.log(CLIENTS_URL);
    return fetch(CLIENTS_URL, {
      method: 'get',
      headers: { 'Content-Type': 'application/json' },
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
        return error;
      });
  }

