  static searchAuditLogs(
    clientId: string,
    limit: number,
    skip: number,
    where: any,
    query?: string,
    order: string[] = []
  ) {

    let filters = {
      limit: limit,
      skip: skip,
    };

    if (query && query.length > 0) {
      filters['search'] = query;
    }

    // console.log(clientId)

    filters['where'] = {
      client_id: clientId
    }

    if (where) {
      where.map(w =>
        filters['where'][w.columnName] = w.value
      )
    }

    if (order.length === 1) {
      filters['order'] = order[0];
    } else {
      filters['order'] = order;
    }

    const AUDIT_LOGS_URL = `${API_URL}audit-logs?filter=${JSON.stringify(filters)}`;

    return fetch(AUDIT_LOGS_URL, {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' }
    })
      .then(response => response.json())
      .catch(error => {
        console.log('client error: ', error);
        return error;
      });
  }

